.feedSection {
    display: grid;
    gap: 1rem;

    padding: 1rem;
}

.feedSection h3 {
    margin: 0;
    padding: 0;
    border-block-end: 2px solid hsl(210, 60%, 90%);
}

.feedCard {
    max-width: clamp(250px, 300px, 400px);
}

.feedCard video {
    width: 100%;
    max-width: 100%;
}

.feedSection img {
    width: 100%;
    max-width: 100%;
}

.feedSection input {
    padding: 0.68rem;
    border-radius: 2px;
}

.feedContainer {
    display: flex;
    flex-wrap: wrap;
    gap: 1rem;

    align-items: flex-start;
    justify-content: center;
}
