.list {
    display: grid;
    gap: 0.64rem;
    align-content: flex-start;

    padding: 1rem;
    border: 2px solid hsl(210, 60%, 90%);

    block-size: 100%;
}

.list input {
    padding: 0.68rem;
    border-radius: 2px;
}

.list h3 {
    margin: 0;
    padding: 0;
}

.list ul {
    padding: 0;
    padding-inline-start: 1rem;
    list-style: none;
}

.list ul li {
    cursor: pointer;
    margin-block: 0.24rem;
}

.list ul li::before {
    content: '@';
    padding-inline-end: 0.24rem;
}

.selectedItem {
    color: hsl(210, 90%, 50%);
    font-weight: 600;
}
